<template>
    <div class="mb-3 align-self-start" id="main"></div>
    <div id="buttons">
        <button id="downloadImg">Download Charts</button>
        <button id="downloadResults">Download Results</button>
        <button id="return">Return back to main menu</button>
    </div>
</template>

<script>
    import $ from 'jquery'
    import * as echarts from "echarts";
    export default {
        name: "TSChart",
        data(){
            return{
                codeImg: ""

            }
        },
        created() {
            let arg=JSON.parse(localStorage.getItem('key'))
            console.log(arg)
            let data1=[]
            let data2=[]
            for (var i=0;i<arg.y1.length;i++){
                data1.push([i+1,arg.y1[i]])
                data2.push([i+1,arg.y2[i]])
            }
            // let base = +new Date(1988, 9, 3);
            // let oneDay = 24 * 3600 * 1000;
            // let data = [[base, Math.random() * 300]];
            // for (let i = 1; i < 20000; i++) {
            //     let now = new Date((base += oneDay));
            //     data.push([+now, Math.round((Math.random() - 0.5) * 20 + data[i - 1][1])]);
            // }
            const option = {
                toolbox: {
                    show:true,
                    right: '20px',
                    feature: {
                        dataZoom: {
                            yAxisIndex: 'none'
                        },
                        restore: {},
                        saveAsImage: {
                            show:true,
                            title: 'Download image'
                        }
                    }
                },
                title: {
                    text: 'Dynamic Data & Time Axis'
                },
                tooltip: {
                    trigger: 'axis',
                    formatter: function (params) {
                        params = params[0];
                        var date = new Date(params.name);
                        return (
                            date.getDate() +
                            '/' +
                            (date.getMonth() + 1) +
                            '/' +
                            date.getFullYear() +
                            ' : ' +
                            params.value[1]
                        );
                    },
                    axisPointer: {
                        animation: false
                    }
                },
                xAxis: {
                    type: 'time',
                    splitLine: {
                        show: false
                    }
                },
                yAxis: {
                    type: 'value',
                    boundaryGap: [0, '100%'],
                    splitLine: {
                        show: false
                    }
                },
                series: [
                    {
                        name: 'One',
                        type: 'line',
                        showSymbol: false,
                        data: data1
                    },
                    {
                        name: 'Two',
                        type: 'line',
                        showSymbol: false,
                        data: data2
                    }
                ]
            };
            $(document).ready(function(){
                const chart1=echarts.init(document.getElementById("main"))
                chart1.setOption(option)

                const canvas = document.querySelector("#main canvas")
                const dataUrl = canvas.toDataURL();
                $('#downloadImg').on("click",function(){
                    doViewAndPrint(dataUrl)
                })
            })

            function doViewAndPrint(canvasUrl) {
                const domPrint = document.createElement('img');
                domPrint.src = canvasUrl;
                let page = window.open('', '_blank'); // 打开一个新窗口，用于打印
                page.document.body.appendChild(domPrint);

                domPrint.onload = function () {
                    page.print(); // 打印
                    page.close(); // 关闭打印窗口
                };
            }

        },
        methods:{
            getImgCode(){
                let url=localStorage.getItem('key')
                localStorage.clear()
                this.codeImg="data:image/png;base64,"+url
            }
        }
    }
</script>

<style scoped>
    #main{
        width: 1000px;
        height:400px;
        margin: 20px;
    }
    #buttons{
        width: 200px;
        height: 400px;
    }
</style>